  /* Markdown 
  ---------------------------*/
  
  .markdown a {
    color: #5e7668;
  }
  
  .markdown p {
    padding-top: 10px;
    padding-right: 40px;
    padding-bottom: 10px;
    padding-left: 20px;
    text-align: justify;
  }
  
  .markdown h1,
  .markdown h2 {
    padding-top: 35px;
    padding-bottom: 0;
    padding-right: 20px;
    padding-left: 20px;
  }
  
  .markdown h3,
  .markdown h4,
  .markdown h5,
  .markdown h6,
  .markdown h3,
  .markdown h4,
  .markdown h5,
  .markdown h6 {
    padding-top: 20px;
    padding-bottom: 0;
    padding-right: 20px;
    padding-left: 20px;
  }
  
  .markdown h6:last-child {
    padding-top: 20px;
    padding-bottom: 40px;
    padding-right: 20px;
    padding-left: 20px;
  }
  
  .markdown li {
    padding-bottom: 10px;
    padding-right: 20px;
  }
  
  .markdown hr {
    width: 50%;
    text-align: left;
    margin-left: 2%;
  }
  
  .markdown table {
    border-collapse: collapse;
    width: 100%;
    display: block;
    margin: 0 auto;
    padding-top: 10px;
    padding-right: 40px;
    padding-bottom: 10px;
    padding-left: 20px;
  }
  
  .markdown th,
  .markdown td {
    text-align: left;
    padding: 8px;
  }
  
  .markdown tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .markdown tr:last-child {
    background-color: #f2f2f2;
    border-bottom: solid thin var(--green-green-beige);
  }
  
  .markdown th {
    background-color: var(--green-green-beige);
    color: white;
  }
  
  .markdown tbody {
    border-bottom: solid thin #f2f2f2;
  }
  
  .markdown pre {
    padding: 20px;
  }
  
  .markdown code {
    font-family: "Courier New", Courier, monospace;
    background-color: #f2f2f2;
  }
  
  .markdown blockquote {
    border-left: solid thick var(--green-beige);
  }
  
  .markdown ul:last-child {
    padding-bottom: 30px;
  }