/* Section Bar
---------------------------*/
.sectionBar {
    font-size: 2em;
    border-bottom-style: solid;
    border-bottom-width: thin;
    color: var(--green-green-beige);
    background-color: white;
    text-align: center;
    text-transform: uppercase;
    padding-top: 40px;
    padding-right: 20px;
    padding-left: 20px;
    margin-top: 0px;
    margin-right: 0px;
    margin-bottom: 2%;
    margin-left: 0px;
  }
  
  
  
  @media screen and (min-width: 735px) {
    .sectionBar {
      font-size: 2.5em;
    }
  }
  @media screen and (min-width: 1070px) {
    .sectionBar {
      font-size: 3em;
    }
  }
/* Header Menu - Inside Page Menu
-----------------------------------*/

.pageMenu {
    visibility: none;
    opacity: 0;
    background-color: var(--green-green-beige);
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: -webkit-min-content auto;
    grid-template-rows: min-content auto;
  }
  
  .pageMenu.visible {
    visibility: visible;
    opacity: 1;
    z-index: 2;
  }
  
  .pageMenu ul {
    list-style-image: none;
    list-style-type: none;
    list-style-position: outside;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
  }
  
  .pageMenu li {
    text-align: center;
    font-size: 2em;
    padding: 1%;
    color: white;
  }
  
  .pageMenu li a {
    text-decoration: none;
    color: white;
    padding-bottom: 10px;
  }
  
  /* animated line under menu item on focus */
  .pageMenu li a:after {
    content: "";
    display: block;
    margin: auto;
    height: 2px;
    width: 0px;
    background: transparent;
    transition: width 0.5s ease, background-color 0.5s ease;
  }
  
  /* animated line under menu item on focus */
  .pageMenu li a:hover:after {
    width: 33%;
    background: white;
  }
  
  .pageMenu .closeMenu {
    color: white;
    padding: 14px 16px;
    margin-top: 2%;
    margin-left: 2%;
    justify-self: start;
    font-size: 1.5em;
  }
  
  .pageMenu .closeMenu:hover {
    background-color: white;
    color: var(--green-green-beige);
    cursor: pointer;
  }
  
